<template name='infoSplit'>
	<view class='layout-split'>
		<view class='split-line' v-if='Split.Line'></view>
		<view class='split-way' v-if='Split.Way'></view>
	</view>
</template>

<script>
	export default({
		name: "infoSplit",
		props: {
			data: {
				type: Object,
				default: function() {
					return {};
				}
			}
		},
		mounted() {
			console.log("infoSplit父组件data定义的变量", this.data);
			console.log("data", this.Split);
		},
		data() {
			return {
				Split: this.data.Split
			}
		}
	})
</script>

<style>
	.layout-split {
		position: relative;
		margin-top: 10px;
		width: 100%;
		height: 1px;
	}
	.split-line {
		position: absolute;
		left: 0px;
		right: 0px;
		top: 0px;
		bottom: 0px;
		background: #EEEEEE;
	}
	.split-way {
		position: absolute;
		left: 92px;
		right: 0px;
		top: 0px;
		bottom: 0px;
		background: #EEEEEE;
	}
</style>
